.btn {
  border-radius: 100px;
  color: #ffffff;
  cursor: pointer;
  display: inline-block;
  font-family: "Roboto", Helvetica, Arial, sans-serif;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 0.03125rem;
  user-select: none;
  min-width: 160px;
  outline: none;
  border: 0;
  transition: 0.2s cubic-bezier(0.55, 0, 0.1, 1);
  min-height: 69px;
  height: 69px;
  line-height: 69px;
  padding: 0 30px; }

