.dropdown {
  position: relative; }

.dropdown-menu {
  display: none;
  background-color: #ffffff;
  top: 100%;
  left: 0;
  position: absolute;
  min-width: 230px; }

.dropdown:hover .dropdown-menu {
  display: block; }

.field {
  display: flex; }

.field > *:focus {
  outline: none; }

.field > input {
  appearance: none;
  border: 1px solid transparent;
  font-family: "Roboto", Helvetica, Arial, sans-serif;
  padding: 15px 0;
  width: 84%; }

.field > .icon {
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 16%; }

.field-lg > input {
  color: #9c9e9f;
  font-size: 1.25rem;
  line-height: 1.5rem;
  letter-spacing: 0;
  font-weight: 400;
  transition: 0.2s cubic-bezier(0.55, 0, 0.1, 1);
  margin-top: 0;
  margin-bottom: 0; }

